html, body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background-color: #000;
  color: #fff;
}
body.bg-black {
  background-color: #000;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  color: #fff;
}

.player-app {
  position: relative;
  margin: auto;
}
.player-app .pixi-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 0;
}
.player-app .pixi-wrapper canvas {
  width: 100%;
  height: 100%;
}

@keyframes popIn {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.dialogue-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  /* Animate the background color from white to yellow */
}
.dialogue-overlay .balloon {
  display: none;
  position: absolute;
  left: 1em;
  width: calc(100% - 2em);
  height: calc(33% - 2em);
  background-color: #fff;
  color: #000;
  border-radius: 1em;
  border: 0.5em solid #000;
  margin: 0;
  padding: 1em;
  font-size: 1.5em;
}
.dialogue-overlay .balloon.top {
  top: 1em;
}
.dialogue-overlay .balloon.bottom {
  bottom: 1em;
}
.dialogue-overlay .balloon.visible {
  display: block;
  animation: popIn 0.25s ease-in-out;
}
@keyframes strikeDown {
  0% {
    transform: translateY(0.25em);
  }
  45% {
    transform: translateY(-0.15em);
  }
  90% {
    transform: translateY(0.25em);
  }
  100% {
    transform: translateY(0.25em);
  }
}
.dialogue-overlay .balloon-speech.press-to-continue:after {
  content: " ";
  display: block;
  position: absolute;
  bottom: -1.15em;
  left: 50%;
  width: 2em;
  height: 2em;
  margin-left: -1em;
  background-image: url("../../static/icons/arrow-triangle-down.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  animation: strikeDown 0.75s ease-in-out infinite;
}
@keyframes flash {
  0% {
    background-color: #fff;
  }
  100% {
    background-color: #ffe34b;
  }
}
.dialogue-overlay .response-option {
  padding-left: 2em;
}
.dialogue-overlay .response-option.selected:before {
  content: " ";
  display: block;
  position: absolute;
  width: 2em;
  height: 2em;
  margin-left: -2em;
  margin-top: -0.2em;
  background-image: url("../../static/openmoji/backhand-index-pointing-right.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.dialogue-overlay .response-option.selected .text {
  background-color: #fff;
  animation: flash 0.5s ease-out forwards;
}
.dialogue-overlay .response-option .text {
  padding: 0 0.1em;
}

.speech-text span {
  opacity: 0;
}
.speech-text span.revealed {
  opacity: 1;
}

.fatal-error {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(92, 6, 6, 0.85);
  background-image: url("../../static/fa/exclamation-triangle-solid.svg");
  background-size: 100px 100px;
  background-repeat: no-repeat;
  background-position: center 50px;
  padding-top: 160px;
}
.fatal-error .fatal-error-text {
  color: white;
  font-size: 20px;
  text-align: center;
  top: 100px;
  left: 50%;
  width: 600px;
  margin: auto;
  margin-bottom: 0.5em;
  padding-left: 15px;
  padding-right: 15px;
}
.fatal-error .fatal-error-details {
  color: white;
  font-size: 16px;
  text-align: center;
  width: 600px;
  margin: auto;
  padding-left: 15px;
  padding-right: 15px;
}

html.with-fatal-error {
  width: 100%;
  height: 100%;
}
html.with-fatal-error body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.connection-state-view {
  display: none;
  pointer-events: none;
  position: absolute;
  width: 300px;
  padding-top: 135px;
  top: 50%;
  left: 50%;
  margin-top: -150px;
  margin-left: -150px;
  color: black;
  font-size: 20px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
}
.connection-state-view.visible {
  display: block;
}
.connection-state-view .connection-state-view-icon {
  display: block;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 25px;
  left: 100px;
}
.connection-state-view .connection-state-view-error {
  font-weight: bold;
}
.connection-state-view .connection-state-view-status {
  font-size: 0.85em;
  margin-bottom: 1.2em;
}