@mixin RobotoRegular {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}

@mixin RobotoBold {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
}

@mixin RobotoCondensedRegular {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 400;
}

@mixin RobotoCondensedBold {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 700;
}
